body {
  display: flex;
  padding: 20px;
  align-items: center;
  justify-content: center;
  margin: 0;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  background: rgb(131, 58, 180);
  background: linear-gradient(
    90deg,
    rgba(131, 58, 180, 1) 0%,
    rgba(253, 29, 29, 1) 50%,
    rgba(252, 176, 69, 1) 100%
  );
}
ul {
  list-style-type: none;
  width: 400px;
}
